$width-tablet: 540px;
$width-pc: 900px;

.section {
  //background-color: #fafafa;
  //border-radius: 4px;
  //border: 1px solid #fff;
  //box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  //margin: 16px auto;
  margin: 16px auto;
  padding: 16px;
  width: 100%;
  max-width: 720px;

  h3 {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 8px;
    border-bottom: 1px solid #ccc;
  }

  article {
    display: flex;
    flex-wrap: wrap;
    margin-top: 16px;
    padding-left: 12px;

    div {
      $date-width: 72px;
      line-height: 23px;

      &:nth-child(1) {
        width: 100%;
        color: #bbb;
        font-weight: bold;

        @media screen and (min-width: $width-tablet) {
          width: #{$date-width};
        }
      }

      &:nth-child(2) {
        width: 100%;

        @media screen and (min-width: $width-tablet) {
          width: calc(100% - #{$date-width});
        }
      }

      a {
        color: #369;
        text-decoration: none;
      }
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      border-radius: 2px;
      background-color: rgba(120, 200, 160, 0.3);
    }
  }
}
